.btn{
    display: inline-block;
    height: 23px;
    line-height: 25px;
    box-shadow: 0 3px 0 #3f6bba;
    border-radius: 6px;
    background-color: #68a2f7;
    color: #fff;
    font-size: 13px;
    font-weight: 400;
    padding: 0 20px;
    transition: .3s;
    &:hover{
        opacity: .7;
    }
    &--big{
        height: 33px;
        line-height: 35px;
        font-size: 16px;
        font-weight: 400;
        border-radius: 5px;
        color: #ffffff;
    }
}


.btn-red{
    height: 37px;
    line-height: 40px;
    box-shadow: 0 3px 0 #b42e5d;;
    border-radius: 5px;
    background-color: #e73d7d;
    white-space: nowrap;
    color: #fff;
    font-size: 16px;
    padding: 0 20px;
    transition: .3s;
    &:hover{
        opacity: .7;
    }
    &--small{
        height: 30px;
        line-height: 30px;
        padding: 0 12px;
    }
    &--big{
        display: inline-block;
        //height: 50px;
    }
}

