.nav{
    //border: 1px solid #000;
    @media (max-width: 768px) {
        //margin-bottom: 20px;
        //width: 700px;
        //position: relative;
        //z-index: 5;
    }
    &-item{
        text-align: center;
        position: relative;
        padding-bottom: 10px;
        @media (max-width: 768px) {
            //margin-bottom: 20px;
            padding-bottom: 5px;
        }
        &:after{
            content: '';
            background: #e6285c;
            height: 2px;
            width: 0;
            transition: .3s;
            position: absolute;
            left: 50%;
            bottom: 0;
            @media (max-width: 768px) {
                display: none;
            }
        }
        &-img{
            height: 40px;
            display: block;
            @media (max-width: 768px) {
                display: none;
            }
            img{
                display: inline-block;
            }
            svg{
                display: inline-block;
                max-width: 45px;
                max-height: 35px;
                transition: .3s;
                path, polygon{
                    fill: #a9b5c2;
                    transition: .3s;
                }
            }
        }
        &-title{
            transition: .3s;
            white-space: nowrap;
        }
        &:hover{
            &:after{
                width: 100%;
                left: 0;
            }
            .nav-item-img{
                svg{
                    fill: #e6285c;
                    //path, polygon{
                    //
                    //}
                }
            }
        }
    }
}

