.trust{
    position: relative;
    &:hover{
        .trust-content{
            opacity: 1;
            pointer-events: auto;
        }
    }
    &-title{

    }
    &-content{
        white-space: nowrap;
        position: absolute;
        top: 30px;
        left: 30px;
        opacity: 0;
        transition: .3s;
        z-index: 5;
        pointer-events: none;
        &:before{
            content: '';
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 10px solid #fff;
            position: absolute;
            top: -10px;
