.voting{
    display: flex;
    margin-left: auto;
    &__btn{
        width: 23px;
            height: 23px;
            border-radius: 11px;
            color: #fff;
            transition: .3s;

            font-size: 17px;
            font-weight: 700;
            padding: 0;
        position: relative;
        &:before{
            content: '';
        }
        &--plus{
            &:before{
                content: '+';
                position: absolute;
                top: 0;
                left: 7px;
            }
        }
        &--minus{
            &:before{
                content: '–';
                position: absolute;
                top: -1px;
                left: 6px;
            }
        }
        &:hover{
            opacity: .7;
        }
    }
    &__result{}
}

