.header{
  &-info{
    &-logo{
      img{
        display: block;
        @media (max-width: 768px) {
          width: 64px;
          height: 44px;
        }
      }
    }
  }
}

.modal-menu{
  .header{
    &-info{
      &-logo{
        img{
          width: 47px;
          height: 43px;
        }
      }
    }
  }
}

.lang{
  height: 24px;
  border-radius: 12px;
  background: #4f85d5;
  a{
    display: inline-block;
    height: 24px;
    line-height: 22px;
    color: #ffffff;
    font-size: 14px;
    border-radius: 12px;
    text-transform: uppercase;
    padding: 0 7px;
    border: 2px solid transparent;
    &.active{
      font-weight: 700;
      border: 2px solid #fff;
    }
  }
}

.city-marker{
  position: relative;
  svg{
    position: absolute;
    top: 3px;
    left: -20px;
  }
}

.city-list-header{
  border-bottom: 1px dashed #fff;
  line-height: 1;
  cursor: pointer;
  position: relative;
  opacity: .9;
  svg{
    position: absolute;
    top: 2px;
    right: -16px;
  }
  &:hover{
    border-bottom: 1px dashed transparent;
  }
}
.logo{
  min-width: 64px;
}

